import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import { useEffect, useState, useMemo } from 'react';
import About from './About';
import './App.css';
import Home from './Home';

function App() {
  const [pokemon, setPokemon] = useState([]);
  const [filteredPokemon, setFilteredPokemon] = useState([]);
  const [text, setText] = useState('');
  useEffect(() => {
    fetch('https://pokeapi.co/api/v2/pokemon?offset=0')
    .then((res) => res.json())
    .then((data) => {
      const results = data.results.map((pokemon, idx) => {
        return {...pokemon, idx: idx + 1}
      })
      setPokemon({...data, results})
    })
  }, [])

  useMemo(() => {
    if(text.length === 0) {
      setFilteredPokemon([])
      return;
    }
    setFilteredPokemon(() => pokemon.results?.filter((pokemon) => pokemon.name.includes(text)))
  }, [pokemon.results, text])
  return (
      <Router>
        <header className="border-b md:flex md:items-center md:justify-center p-4 pb-0 shadow-lg md:pb-4">
          <div className="flex items-center justify-center mb-4 md:mb-0">
            <h1 className="leading-none text-2xl text-grey-darkest">
              <Link to="/" className="no-underline text-red-600 hover:text-black">
                Pokemon
              </Link>
            </h1>
          </div>
        </header>
  
      <Switch>
        <Route exact path="/">
          <div className="w-full flex justify-center">
            <input type="text"
                  onChange={($event) => setText($event.target.value)} 
                  placeholder="Enter Pokemon here"
                  className="mt-10 p-2 border-blue-500 border-2"
            />
          </div>
          {
            pokemon &&
            <Home pokemon={filteredPokemon}></Home>
          }
        </Route>
        <Route path="/about/:slug">
          <About></About>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
